<template>
  <div class="container">
    <h2 class="text-center m-4"><i class="fas fa-lightbulb mr-2"></i></h2>
    <p><br /><br /></p>
    <div class="form-group">
      <input v-model="data.request.title" type="text" class="form-control" id="title" placeholder="" />
    </div>
    <div class="form-group">
      <textarea v-model="data.request.content" class="form-control" id="content" rows="10" placeholder=""></textarea>
    </div>
    <div class="text-right">
      <button @click="postRequest()" class="btn btn-primary mb-3 mb-md-5"><i class="fas fa-reply mr-2"></i></button>
    </div>

    <div class="mb-3 mb-md-5">
      <h2 class="text-center m-4"><i class="fas fa-lightbulb mr-2"></i></h2>
      <template v-for="(request, key) in $store.state.requests" :key="key">
        <div class="card mb-2 mb-md-4">
          <div class="card-body">
            <h5>{{ request.title }}</h5>
            <p>{{ request.content }}</p>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import { reactive, onMounted } from "vue";
import axios from "axios";
// import { useRouter } from "vue-router";
import { useStore } from "vuex";

export default {
  components: {},
  props: {},
  setup() {
    // const router = useRouter();
    // const route = useRoute();
    const store = useStore();

    onMounted(() => {
      store.dispatch("getRequests");
    });

    const data = reactive({
      request: {
        title: "",
        content: "",
      },
    });

    function postRequest() {
      if (data.request.title == "" || data.request.content == "") {
        store.commit("setAlert", {
          flag: {
            showSuccessAlert: false,
            showErrorAlert: true,
            showWarningAlert: false,
          },
          message: {
            error: "",
          },
        });
      } else {
        axios
          .post(
            `${process.env.VUE_APP_CONNECT_BACKEND_URL}/requests/create`,
            {
              request: {
                title: data.request.title,
                content: data.request.content,
              },
            },
            { withCredentials: true }
          )
          .then((response) => {
            if (response.data.created_request) {
              store.dispatch("getRequests");
              store.commit("setAlert", {
                flag: {
                  showSuccessAlert: true,
                  showErrorAlert: false,
                  showWarningAlert: false,
                },
                message: {
                  success: "",
                },
              });
              data.request.title = "";
              data.request.content = "";
            } else {
              store.commit("setAlert", {
                flag: {
                  showSuccessAlert: false,
                  showErrorAlert: true,
                  showWarningAlert: false,
                },
                message: {
                  error: "",
                },
              });
            }
          })
          .catch((e) => {
            alert(e);
          });
      }
    }

    return {
      data,
      postRequest,
    };
  },
};
</script>

